<template>
  <div style="text-align: center"  >
      
      <el-form
            ref="adminInfo"
            :model="adminInfo"
            label-width="120px"
            style="width: 600px"
        >

        <el-form-item label="姓名" prop="name" :rules="[ { required: true, message: '姓名必须填写,如 张老师' },]">
            <el-input v-model.trim="adminInfo.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="账号" prop="username" :rules="[ { required: true, message: '账号必须写, 即 登录账号' },]" >
            <el-input v-model.trim="adminInfo.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="密码" prop="password" :rules="[ { required: true, message: '密码鼻血写, 即 登录密码' },]"> 
            <el-input v-model.trim="adminInfo.password" autocomplete="off"></el-input>
        </el-form-item>


        <el-form-item>
        <el-button type="primary" @click="submitForm('adminInfo')">添加</el-button>
        <el-button @click="resetForm('adminInfo')">重置</el-button>
        </el-form-item>

    </el-form>



  </div>
</template>

<script>
import { ElMessage } from 'element-plus'
export default {
    name: "AdminSetUp",
    data() {
        return {
            adminInfo: {
                username: "",
                password: "",
                name: "",
            }
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios.post("/user/addAdmin", this.adminInfo).then(res => {
                        const data = res.data
                        if (data.code == 200 && data.data == true) {
                            ElMessage.success('添加成功', {duration: 3 * 1000})
                            this.resetForm('adminInfo')
                        } else {
                            ElMessage.error(data.msg, {duration: 3 * 1000})
                        }
                    })

                } else {
                    ElMessage.error('参数有错误', {duration: 3 * 1000})
                    return false
                }
            })
      },

      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
    }
}
</script>

